import React from 'react'

import Tabs from 'react-scroll-snap-tabs'
import 'react-scroll-snap-tabs/dist/index.css'
import './index.css'
import {
  BsFillGearFill,
  BsGear,
  BsFillHouseFill,
  BsHouse,
  BsFillEnvelopeFill,
  BsEnvelope
} from 'react-icons/bs'

const App = () => {
  return <ElasticIndicator></ElasticIndicator>
}

export default App

function AdvancedExample() {
  return (
    <div style={{ width: '400px', height: '100vh' }}>
      <Tabs>
        <Tabs.Content style={{ backgroundColor: 'rgb(240,240,240)' }}>
          <Tabs.Pane eventKey='tab1'>Content 1</Tabs.Pane>
          <Tabs.Pane eventKey='tab2'>Content 2</Tabs.Pane>
          <Tabs.Pane eventKey='tab3'>Content 2</Tabs.Pane>
        </Tabs.Content>
        <Tabs.Nav
          indicatorParentStyle={{ bottom: 'auto', top: '50%', transform: 'translateY(-50%)' }}
          indicatorStyle={{ height: '34px', borderRadius: '10px' }}
          indicatorColor='#3A4750'
          indicatorSize='4em'
          linkStyle={{ flexGrow: 1, display: 'flex', justifyContent: 'center' }}
          activeLinkClass='active'
          linkClass='tab-link'
          style={{ backgroundColor: 'white', padding: '5px' }}
        >
          <Tabs.Link eventKey={'tab1'}>
            <BsFillHouseFill color='#343434' size='1.5em' className='active-icon' />
            <BsHouse color='#343434' size='1.5em' className='icon' />
          </Tabs.Link>
          <Tabs.Link eventKey={'tab2'}>
            <BsFillEnvelopeFill size='1.5em' className='active-icon' />
            <BsEnvelope size='1.5em' className='icon' />
          </Tabs.Link>
          <Tabs.Link eventKey={'tab3'}>
            <BsFillGearFill size='1.5em' className='active-icon' />
            <BsGear size='1.5em' className='icon' />
          </Tabs.Link>
        </Tabs.Nav>
      </Tabs>
    </div>
  )
}

function CustomComponent({ children, color }) {
  return (
    <div
      style={{
        backgroundColor: 'transparent',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        fontStyle: 'bold',
        fontSize: '3vw',
        width: '100%',
        height: '100%',
        color
      }}
    >
      {children}
    </div>
  )
}

function Basic() {
  return (
    <div style={{ width: '400px', height: '100vh', border: '1px solid gray' }}>
      <Tabs
        indicatorParentClass='indicator-parent'
        indicatorParentStyle={{ border: '1px solid red' }}
        indicatorClass='indicator'
        indicatorStyle={{ maxWidth: '75%' }}
        linkClass='link'
        eventKeys={['Tab 1', 'Very Long Tab 2']}
      >
        <Tabs.Content paneStyle={{ border: '1px solid gray' }}>
          <Tabs.Pane eventKey='Tab 1'>Content 1</Tabs.Pane>
          <Tabs.Pane eventKey='Very Long Tab 2'>Content 2</Tabs.Pane>
        </Tabs.Content>
      </Tabs>
    </div>
  )
}

function FullHeihgtIndicatorNav() {
  const height = 34
  const onIndicatorMove = (e) => {
    const indicator = e.target
    const timing = (t) => (t <= 0.2 ? -4.5 * t + 1 : t < 0.8 ? 0.1 : 4.5 * (t - 1) + 1)
    const progress = timing(e.progress)
    indicator.style.height = progress * height + 'px'
  }
  return (
    <div style={{ width: '400px', height: '100vh' }}>
      <Tabs
        style={{ borderRight: '1px solid gray' }}
        layout='horizontal'
        snapDuration={250}
        easing='ease-in-out'
        indicatorSize='20px'
        defaultKey='tab3'
      >
        <Tabs.Nav
          activeLinkStyle={{ color: '#5A90E4' }}
          activeLinkClass='active-link'
          indicatorStyle={{
            border: '1px solid lightgray',
            borderBottom: 'none',
            borderTop: '2px solid gray',
            borderRadius: '4px',
            borderBottomLeftRadius: '0',
            borderBottomRightRadius: '0',
            height: height + 'px',
            maxHeight: '100%',
            backgroundColor: 'white'
          }}
          linkStyle={{
            whiteSpace: 'nowrap',
            color: 'black',
            flexGrow: '1',
            borderBottom: '1px solid lightgray'
          }}
          style={{ color: 'gray', backgroundColor: '#f0f0f0' }}
          onIndicatorMove={onIndicatorMove}
          className='tab-nav'
        >
          <Tabs.Link eventKey='tab1'>
            <span>Tab 1</span>
          </Tabs.Link>
          <Tabs.Link eventKey='tab2'>
            <span>Tab 2</span>
          </Tabs.Link>
          <Tabs.Link eventKey='tab3'>
            <span>Tab 3</span>
          </Tabs.Link>
          <Tabs.Link eventKey='tab4'>
            <span>Long Tab 4</span>
          </Tabs.Link>
          <Tabs.Link eventKey='tab5'>
            <span>Tab 5</span>
          </Tabs.Link>
          <Tabs.Link eventKey='tab6'>
            <span>Tab 6</span>
          </Tabs.Link>
          <Tabs.Link eventKey='tab7'>
            <span>Long Tab 7</span>
          </Tabs.Link>
        </Tabs.Nav>
        <Tabs.Content
          style={{ overscrollBehaviorY: 'contain' }}
          paneStyle={{ minHeight: '100%', minWidth: '100%' }}
        >
          <Tabs.Pane eventKey='tab1'>
            <CustomComponent color='#B3C890'>Content 1</CustomComponent>
          </Tabs.Pane>
          <Tabs.Pane eventKey='tab2'>
            <CustomComponent color='#73A9AD'>Content 2</CustomComponent>
          </Tabs.Pane>
          <Tabs.Pane eventKey='tab3'>
            <CustomComponent color='#F5F0BB'>Content 3</CustomComponent>
          </Tabs.Pane>
          <Tabs.Pane eventKey='tab4'>
            <CustomComponent color='#643843'>Content 4</CustomComponent>
          </Tabs.Pane>
          <Tabs.Pane eventKey='tab5'>
            <CustomComponent color='#B3C890'>Content 5</CustomComponent>
          </Tabs.Pane>
          <Tabs.Pane eventKey='tab6'>
            <CustomComponent color='#73A9AD'>Content 6</CustomComponent>
          </Tabs.Pane>
          <Tabs.Pane eventKey='tab7'>
            <CustomComponent color='#F5F0BB'>Content 7</CustomComponent>
          </Tabs.Pane>
        </Tabs.Content>
      </Tabs>
    </div>
  )
}

